import React from "react";
import { DndProvider, useDrag, useDrop } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import {
  FormProvider,
  useFieldArray,
  useForm,
  useFormContext
} from "react-hook-form";

type MoviesType = {
  movie: {
    title: string;
    description: string;
    id: string;
  };
  position: number;
  moveCard: (from: number, to: number) => void;
};

const Items = ({ movie, position, moveCard }: MoviesType) => {
  // FormProviderã«ãã£ã¦ããã§åãåããç°¡åã«
  const methods = useFormContext();

  //useDropã¨useDragãä½¿ç¨ããdropã¨dragãå¾ãã
  //æä½éä¸¦ã³æ¿ãã§ããã®ãä»¥ä¸ã®å®è£
  const [, drop] = useDrop({
    accept: "CARD",

    //useDragã®itemãã£ã¼ã«ãã§æå®ããé ç®ãåç§ã§ãã
    hover: (item: Record<string, any>) => {
      const dragIndex = item.index; //dragä¸­ã®ã¤ã³ããã¯ã¹
      const hoverIndex = position; //hoverå¯¾è±¡ã®ã¤ã³ããã¯ã¹

      if (dragIndex === hoverIndex) {
        return;
      }
      moveCard(dragIndex, hoverIndex);
      item.index = hoverIndex;
    }
  });

  const [{ isDragging }, drag, preview] = useDrag(
    () => ({
      type: "CARD",
      item: { index: position, id: movie.id, type: "CARD" },
      collect: (monitor) => {
        return {
          isDragging: monitor.isDragging()
        };
      }
    }),
    [position]
  );

  return (
    <div
      //dropã¨previewãçµã³ã¤ãããã¨ã§previewå¯¾è±¡ã«
      // previewã ã¨dragã¯ã§ããªã
      ref={(node) => preview(drop(node))}
      style={{
        opacity: isDragging ? 0.5 : 1,
        fontSize: 25,
        cursor: "pointer",
        display: "flex"
      }}
    >
      <input {...methods.register(`items[${position}].title`)} />
      <input {...methods.register(`items[${position}].description`)} />
      {/*
          //dropã¨dragãçµã³ã¤ãããã¨ã§dragå¯¾è±¡ã«
        */}
      <button ref={(node) => drag(drop(node))}>XX</button>
    </div>
  );
};

export default () => {
  // formã®å®ç¾©
  const methods = useForm({
    defaultValues: {
      test: "test",
      items: [
        { id: "1xx", title: "Movie barato", description: "Lorem ipsum 1" },
        { id: "2bb", title: "Movie mÃ©dio", description: "Lorem ipsum 2" },
        { id: "3ee", title: "Movie caro", description: "Lorem ipsum 3" }
      ]
    }
  });

  // éåé¨åã®ç®¡çå®£è¨
  // controlãä¸è¨formã§ãããã¨ãç´ä»ãã¦ã
  const { fields, move } = useFieldArray({
    control: methods.control,
    name: "items"
  });

  const onSubmit = (data: any) => console.log(data);

  return (
    // FormProviderã«ãã£ã¦å­ã³ã³ãã¼ãã³ãã«methodsãæ¸¡ãããããã¦ã
    <FormProvider {...methods}>
      <form onSubmit={methods.handleSubmit(onSubmit)}>
        {/*  registerã§inputã¨ç´ä»ã */}
        <input {...methods.register("test")} />
        <hr />
        {/*
          ä¸¦ã³æ¿ãå¯¾è±¡é¨åãDndProviderã«ãã
          ã¿ããããã¤ã¹ã§ãªãã¨ãã¯HTML5Backendãæå®
          @see https://qiita.com/hiyamamoto/items/d83cf4ccfcfa886a0ba8
        */}
        <DndProvider backend={HTML5Backend}>
          {fields.map((field, index) => (
            <Items
              key={field.id}
              movie={field}
              position={index}
              moveCard={move}
            />
          ))}
        </DndProvider>
        <button type="submit">PUSH</button>
      </form>
    </FormProvider>
  );
};
